.social-media-container {
  padding-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
  width: 100%;
  line-height: 50px;
}

.social-item-container a {
  line-height: 100%;
  vertical-align: middle;
  float: right;
}

a {
  line-height: 0;
  text-decoration: none;
  color: #222525;
  font-weight: bold;
}

a:hover {
  color: #b4b4b4;
}